.btn{
    background-color: transparent;
    cursor: pointer;
    padding: 12px 0;
    border: none;

    &:focus{
        outline: none;
    }

    &--primary{
        background-color: $primary;
        border-radius: 5px;
        color: $white;
        font-size: 16px;
        font-weight: 500;
        transition: all .3s;

        &:hover{
            background-color: lighten($color: $primary, $amount: 15);
        }

    }

    &--outline{
        background-color: transparent;
        border: 1px solid $primary;
        color: $primary; 
        padding: 8px 20px;
        transition: all .3s;
        border-radius: 5px;
        
        &:hover{
            background-color: darken($color: $white, $amount: 5);
        }
    }

    

}

.google-btn {
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    height: 42px;
    background-color: $google-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

    transition: box-shadow .3s ease;

    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        background-color: $white;
    }
    .google-icon {
        position: absolute;
        margin-top: 11px;
        margin-left: 11px;
        width: 18px;
        height: 18px;
    }
    .btn-text {
        float: right;
        margin: 11px 40px 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px;
    }
    &:hover {
        box-shadow: 0 0 6px $google-blue;
    }
    &:active {
        background: $button-active-blue;
    }
}