.input{

    border: 1px solid $light-grey;
    border-radius: 8px;
    margin-bottom: 10px;
    overflow: hidden;

    &__field{
        border: none;
        padding:14px 10px;
        font-size: 16px;
        width: 100%;

        &:focus{
            outline: none;
        }
    }    
}